import React, { useEffect, useState } from "react";
import Modal from "../UI/Modal";
import { ajaxCall } from "../../helpers/ajaxCall";
import { useSelector } from "react-redux";
import LoadingData from "../UI/LoadingData";
import { Col, Container, Row } from "react-bootstrap";

function ShowProject(props) {
  const [projectData, setProjectData] = useState(null);
  const authData = useSelector((state) => state.authStore);
  let modalHead, modalBody, modalFooter;
  const getProjectData = async () => {
    const response = await ajaxCall(
      `/alllist/project/${props.proId}/`,
      {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
          Authorization: `Bearer ${authData.accessToken}`,
        },
        method: "GET",
        // signal,
      },
      8000
      // timeoutfun
    );
    console.log(response);
    if (response?.status !== 200) return;
    setProjectData(response.data);
    // setLoadError({ isLoading: false, isError: false, isSubmitting: false });
  };
  useEffect(() => {
    getProjectData(props.proId);
  }, [props.proId]);
  const doGrid = (name, val, cond = false, condVal = null) => {
    if (cond) if (!condVal) return;
    return (
      <Col md={6}>
        <Row>
          <Col md={6}>{name}</Col>
          <Col md={6}>: {val ? val : "N/A"}</Col>
        </Row>
      </Col>
    );
  };
  const doArrayGrid = (name, val, cond = false, condVal = null) => {
    if (cond) if (!condVal) return;
    return (
      <Col md={6}>
        <Row>
          <Col md={6}>{name}</Col>
          <Col md={6}>
            <div className="showFlex">
              <span>:</span>
              {val?.length ? (
                cond ? (
                  <ol>
                    {val.map((val) => (
                      <li>{val[condVal]}</li>
                    ))}
                  </ol>
                ) : (
                  <ol>
                    {val.map((val) => (
                      <li>{val.serial_number}</li>
                    ))}
                  </ol>
                )
              ) : (
                `No ${name} assigned`
              )}
            </div>
          </Col>
        </Row>
      </Col>
    );
  };
  if (projectData) {
    modalHead = <h2>{projectData.project_name}</h2>;
    modalBody = (
      <>
        <Container>
          <Row>
            <h3 className="text-center">General Info</h3>
            <hr />
            {doArrayGrid(
              "Client Name",
              projectData.client,
              true,
              "official_name"
            )}
            {doArrayGrid("Unit Name", projectData.unit, true, "name_of_unit")}
            <hr />
            {doArrayGrid(
              "Reactor Name",
              projectData.reactor,
              true,
              "reactor_name"
            )}
            {doGrid("Project Number", projectData.project_number)}
            <hr />
            {doArrayGrid(
              "Scope of work",
              projectData.scope_of_work,
              true,
              "name"
            )}
            {doGrid("Contract", projectData.contract)}
            <hr />
            {doGrid(
              "Sub contract client name",
              projectData.if_sub_client_name,
              true,
              projectData.contract
            )}
            {doGrid("Remark", projectData.general_remarks)}
          </Row>
          <Row>
            <h3 className="text-center">Project Dates</h3>
            <hr />
            {doGrid("Equipment Preperation Date", projectData.equipment_prep)}
            {doGrid("Equipment Ready Date", projectData.equipment_ready)}
            <hr />
            {doGrid(
              "Equipment Ship Client Date",
              projectData.equipment_ship_client
            )}
            {doGrid(
              "Equipment Delivery Client Date",
              projectData.equipment_delivery_client
            )}
            <hr />
            {doGrid("Project Start Date", projectData.project_start)}
            {doGrid("Project End Date", projectData.project_end)}
            <hr />
            {doGrid(
              "Equipment return client Date",
              projectData.equipment_return_tubemaster
            )}
            {doGrid(
              "Equipment Delivery Date",
              projectData.equipment_delivery_tubemaster
            )}
            <hr />
            {doGrid(
              "Equipment Info Remark",
              projectData.equipment_info_remarks
            )}
          </Row>
          <Row>
            <h3 className="text-center">Assigned Equipments</h3>
            <hr />
            {doArrayGrid("TTD", projectData.ttd)}
            {doArrayGrid("BDD", projectData.bdd)}
            <hr />
            {doArrayGrid("Calibration stand", projectData.calibration_stand)}
          </Row>
          <Row>
            <h3 className="text-center">Assigned Parts</h3>
            <hr />
            {doArrayGrid("Part", projectData.part)}
            {doArrayGrid("Supply Orific", projectData.bdd)}
            <hr />
            {doArrayGrid("Pressure Sensor", projectData.pressure_sensor_part)}
            {doArrayGrid(
              "Calibration Orifice",
              projectData.calibration_orifice_part
            )}
            <hr />
            {doArrayGrid("Swab Master TSR", projectData.swabmaster_part)}
            {doArrayGrid("Device Hose", projectData.device_part)}
            <hr />
            {doArrayGrid("Air Hose", projectData.airhose_part)}
          </Row>
        </Container>
      </>
    );
  }
  return (
    <Modal
      show={props.show}
      close={props.changeStatus}
      isLoading={!projectData}
      showHeader
      showBody
      showFooter
      modalHead={modalHead}
      modalBody={modalBody}
      modalFooter={modalFooter}
    />
  );
}

export default ShowProject;
